<template>
  <div class="w-screen py-24 flex flex-col items-center">
    <div class="text-4xl font-semibold py-px">Mint USDT</div>
    <div class="opacity-80 pb-px">
      Please enter address and amount of USDT you want to mint
    </div>
    <input
      type="text"
      class="value-number-ticket text-center bg-slate-200 px-1 border-none py-1 rounded-2xl text-xl font-semibold w-1/3 h-12 mt-12"
      v-model="addressWallet"
      placeholder="address wallet"
    />
    <input
      type="number"
      class="value-number-ticket text-center bg-slate-200 px-1 border-none py-1 rounded-2xl text-xl font-semibold w-1/3 h-12 mt-4"
      v-model.number="balance"
      placeholder="amount"
    />
    <button
      class="learn-how-btn flex flex-row items-center justify-center font-semibold px-6 py-2 bg-button-background-primary text-custom-white-text rounded-2xl shadow-3xl hover:opacity-80 w-48 mt-6"
      @click="mintUSDT"
    >
      Mint
    </button>
  </div>
</template>
<script>
import { mint } from "@/utils/getUSDTContract";
import { mapActions, mapState } from "vuex";
import Web3 from "web3/dist/web3.min.js";
export default {
  name: "MintView",
  data() {
    return {
      balance: null,
      addressWallet: null,
    };
  },
  computed: {
    ...mapState(["account"]),
  },
  methods: {
    ...mapActions({
      setLoader: "setLoader",
    }),
    async mintUSDT() {
      this.setLoader({ isLoading: true, type: "mint" });
      try {
        await mint(
          this?.account,
          Web3.utils.toChecksumAddress(this?.addressWallet),
          this?.balance
        );
      } catch (err) {
        this.setLoader({ isLoading: false, type: "" });
      }
      this.setLoader({ isLoading: false, type: "" });
    },
  },
};
</script>
<style lang="scss">
.value-number-ticket::-webkit-inner-spin-button,
.value-number-ticket::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
</style>
